}

/*Progress gradient*/
.progress-grad .progress-bar {
  background: linear-gradient(150deg, #97c74e 0%, #2ab9a5 100%);
}

.progress-dark-grad .progress-bar {
  background: linear-gradient(150deg, #343a40 0%, #626d78 100%);
}

/*Progress dark*/
.progress-dark .progress-bar {
  background: #343a40;
}

/*Progress square*/
.progress-square .progress-bar, .progress-square .progress {
  border-radius: 0px;
}

/*Progress percent*/
.percent-bg .progress-percent {
  background: #343a40;
  padding: 2px 5px;
  border-radius: 2px;
  color: #ffffff;
  line-height: 16px;
  position: absolute;
  right: 0;
  top: -25px;
  font-size: 12px;
}
.percent-bg .progress-percent:before {
  border-top-color: #343a40;
  border-width: 5px;
  left: 50%;
  margin-left: -5px;
}
.percent-bg .progress-percent:after, .percent-bg .progress-percent:before {
  top: 100%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.percent-bg .progress-percent:after {
  border-top-color: #343a40;
  border-width: 4px;
  left: 50%;
  margin-left: -4px;
}

/*percent light*/
.percent-light .progress-percent {
  background: #f0f1f3;
  color: #8f9397;
}
.percent-light .progress-percent:before, .percent-light .progress-percent:after {
  border-top-color: #f0f1f3;
}

/*Progress size*/
.progress-xs .progress {
  height: 3px;
}

.progress-sm .progress {
  height: 5px;
}

.progress-md .progress {
  height: 10px;
}

.progress-lg .progress {
  height: 16px;
}

.progress-xl .progress {
  height: 20px;
}

/*progress-text-in*/
.progress-text-in .progress-item {
  position: relative;
  margin-top: 10px;
}
.progress-text-in .progress-item .progress {
  height: 24px;
}
.progress-text-in .progress-item .progress .progress-percent {
  color: #ffffff;
  padding-right: 5px;
  top: 0px;
}
.progress-text-in .progress-item p {
  font-size: 0.8125rem;
  margin: 0;
  position: absolute;
  z-index: 1;
  color: #ffffff;
  top: 2px;
  left: 5px;
}

.progress-text-in .progress-bar:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999999;
  opacity: 0.1;
  background-image: -webkit-linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent);
