/*===============================
      TIMELINE 
=================================*/
.timeline-dot {
  height: 30px;
  width: 30px;
  display: block;
  border: solid 2px #efefef;
  background: #ffffff;
  text-align: center;
  margin: 0 auto;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  left: 0;
  right: 0;
  margin: 0 auto;
}

.timeline-line:before {
  content: "";
  position: absolute;
  width: 2px;
  background: #efefef;
  height: 100%;
  display: block;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
}

.timeline-top:before {
  background: #efefef;
  position: absolute;
  height: 3px;
  width: 50px;
  display: block;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -25px;
}

