  margin-left: 0px;
  float: left;
  margin-right: 10px;
  padding: 0;
  transition: all 0.5s ease-in-out;
}
.feature-box.f-style-4 .feature-box-title {
  margin-top: 0px;
  padding-left: 70px;
}
.feature-box.f-style-4 a {
