/*pricing ribbon*/
.pricing .ribbon {
  position: absolute;
  left: -5px;
  top: -5px;
  z-index: 1;
  overflow: hidden;
  width: 75px;
  height: 75px;
  text-align: left;
}
.pricing .ribbon span {
  font-size: 10px;
  font-weight: 600;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: linear-gradient(150deg, #97c74e 0%, #2ab9a5 100%);
  box-shadow: 0 3px 10px -5px black;
  position: absolute;
  top: 19px;
  left: -21px;
}
.pricing .ribbon span::before {
  content: "";
  position: absolute;
  right: 0px;
  top: 100%;
  z-index: -1;
  border-right: 3px solid #343a40;
  border-left: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #343a40;
}
.pricing .ribbon span::after {
  content: "";
  position: absolute;
  left: 0px;
  top: 100%;
  z-index: -1;
  border-right: 3px solid transparent;
  border-left: 3px solid #343a40;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #343a40;
}

