.process-advance .border-start:before {
  content: "";
  position: absolute;
  top: 50%;
  display: block;
  width: calc(50% + 30px);
  border-top-width: 1px;
  border-top-style: solid;
  margin-top: -1px;
  left: -30px;
  border-color: #dfe2e5 !important;
  z-index: -1;
