/*bg-light*/
.blockquote.bg-light {
  padding: 20px 20px 20px 90px;
}
.blockquote.bg-light:before {
  top: 20px;
  left: 20px;
}

/*primary-line*/
.blockquote.primary-line {
  padding: 10px 20px;
  border-left: 5px solid #4cbd89;
}
.blockquote.primary-line:before {
  display: none;
}

/*gray-line*/
.blockquote.gray-line {
  padding: 10px 20px;
  border-left: 5px solid #f0f1f3;
}
.blockquote.gray-line:before {
  display: none;
}

/*grad-line*/
.blockquote.grad-line {
  padding: 10px 20px;
}
.blockquote.grad-line:before {
  display: none;
}
.blockquote.grad-line:after {
  content: "";
  display: block;
  position: absolute;
  width: 5px;
  height: 100%;
  top: 0px;
  left: 0;
  background: linear-gradient(150deg, #97c74e 0%, #2ab9a5 100%);
}

/*divider*/
.divider {
  color: #8f9397;
  width: 100%;
  margin: 0px auto;
  overflow: hidden;
  text-align: center;
  line-height: 0em;
}
.divider i {
  font-size: 20px;
  margin: 0 20px 0 20px;
  display: inline-block;
  vertical-align: middle;
}
.divider span, .divider a {
  margin: 0 20px 0 20px;
  display: inline-block;
  vertical-align: middle;
}
.divider:before {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  content: "";
  display: inline-block;
  height: 0em;
  margin: 0 0 0 -100%;
  vertical-align: middle;
  width: 50%;
}
.divider:after {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  content: "";
  display: inline-block;
  height: 0;
  margin: 0 0 0 -100%;
  vertical-align: middle;
  width: 50%;
  margin: 0 -100% 0 0;
}

.divider-grad:before {
  height: 3px;
  background: linear-gradient(150deg, #97c74e 0%, #2ab9a5 100%);
}
.divider-grad:after {
  height: 3px;
  background: linear-gradient(150deg, #2ab9a5 0%, #97c74e 100%);
}

.divider-dark:before, .divider-dark:after {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1) !important;
}

.divider-dotted:before, .divider-dotted:after {
  border-bottom: 3px dotted rgba(0, 0, 0, 0.1);
}

.divider-bold:before, .divider-bold:after {
  border-bottom-width: 5px;
}

.divider-small {
  width: 10%;
  margin: 5px auto;
}

.divider-medium {
  width: 20%;
  margin: 10px auto;
}

.divider-large {
  width: 60%;
  margin: 10px auto;
}

