/*hover effect*/
.social-icons .social-icons-link:hover i {
  -webkit-animation: toTopFromBottom 0.3s forwards;
  -moz-animation: toTopFromBottom 0.3s forwards;
  animation: toTopFromBottom 0.3s forwards;
}

@-webkit-keyframes toTopFromBottom {
  49% {
    -webkit-transform: translateY(-100%);
  }
  50% {
    opacity: 0;
    -webkit-transform: translateY(100%);
  }
  51% {
    opacity: 1;
  }
}
@-moz-keyframes toTopFromBottom {
  49% {
    -moz-transform: translateY(-100%);
  }
  50% {
    opacity: 0;
    -moz-transform: translateY(100%);
  }
  51% {
    opacity: 1;
  }
}
@keyframes toTopFromBottom {
  49% {
    transform: translateY(-100%);
  }
  50% {
    opacity: 0;
    transform: translateY(100%);
  }
  51% {
    opacity: 1;
  }
}
/* team */
.team {
  overflow: hidden;
}

.team-item {
  position: relative;
  overflow: hidden;
  margin-bottom: 30px;
}
.team-item .team-desc {
  padding: 15px 10px 10px;
}
.team-item .team-name {
  margin: 0;
}
.team-item .team-name a {
  display: block;
  color: #8f9397;
}
.team-item .team-name a:hover {
  color: #4cbd89;
}
.team-item .team-position {
  font-size: 0.8125rem;
  color: #4cbd89;
}
.team-item p {
  margin: 10px 0px 10px;
}

/*team hover*/
.team.team-overlay .team-item:hover .team-avatar {
  transition: all 1s ease-in-out;
  overflow: hidden;
  position: relative;
}
.team.team-overlay .team-item .team-avatar:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  height: 100%;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  background: linear-gradient(150deg, #97c74e 0%, #2ab9a5 100%);
  transition: all 0.5s ease-in-out;
}
.team.team-overlay .team-item:hover .team-avatar:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  height: 100%;
  width: 100%;
  opacity: 0.7;
  visibility: visible;
  overflow: hidden;
  background: linear-gradient(150deg, #97c74e 0%, #2ab9a5 100%);
  transition: all 0.5s ease-in-out;
}

/*team bordered*/
.team.team-bordered .team-desc {
  border: 1px solid #f0f1f3;
  border-top: 0;
}

/*Team grid*/
.team.team-grid .team-avatar {
  width: 50%;
  float: left;
}
.team.team-grid .team-desc {
  width: 50%;
  float: left;
  padding: 0 15px;
}
.team.team-grid .social-icons {
  position: absolute;
  bottom: 0;
}
.team.team-grid .team-desc p {
  padding-bottom: 30px;
}

/*social icon on hover*/
.team.social-hover .team-item .team-avatar {
  position: relative;
  overflow: hidden;
}
.team.social-hover .team-item .team-desc .social-icons {
  position: absolute;
  top: 5px;
  left: -25px;
  visibility: hidden;
  z-index: 99;
  transition: all 0.3s ease-in-out;
}

.team.social-hover .team-item .social-icons-item {
  display: block;
  margin-left: -50px;
  transition: all 0.5s ease-in-out;
}
.team.social-hover .team-item .social-icons-item a {
  color: #ffffff;
}
.team.social-hover .team-item:hover .social-icons {
  visibility: visible;
  left: 5px;
  transition: all 0.3s ease-in-out;
}

.team.social-hover .team-item:hover .social-icons-item:nth-child(1) {
  margin-left: 0px;
  transition: all 0.5s ease-in-out;
}
.team.social-hover .team-item:hover .social-icons-item:nth-child(2) {
  margin-left: 0px;
  transition: all 0.7s ease-in-out;
}
.team.social-hover .team-item:hover .social-icons-item:nth-child(3) {
  margin-left: 0px;
  transition: all 1s ease-in-out;
}
.team.social-hover .team-item:hover .social-icons-item:nth-child(4) {
  margin-left: 0px;
  transition: all 1.3s ease-in-out;
}
.team.social-hover .team-item:hover .social-icons-item:nth-child(5) {
  margin-left: 0px;
  transition: all 1.5s ease-in-out;
}

@media (max-width: 767.98px) {
  .team.team-grid .team-avatar {
    width: 100%;
    float: none;
  }
  .team.team-grid .team-desc {
    width: 100%;
    float: none;
    padding: 15px 0px 10px;
  }
}
