  border: 2px solid #ffffff;
  box-sizing: border-box;
}
.tns-controls i {
  color: #343a40;
  font-size: 12px;
  transition: all 0.3s ease-in-out;
}

.tns-controls [data-controls=prev] {
  left: 0;
}

.tns-controls [data-controls=next] {
  right: 0;
}

