.tns-nav {
  position: absolute;
  top: 100%;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  width: auto;
  text-align: center;
  white-space: nowrap;
}
.tns-nav [data-nav] {
  position: relative;
  display: inline-block;
  margin: 0 4px;
  padding: 0;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  overflow: hidden;
  background-color: transparent;
  box-shadow: inset 0 0 0 2px #b4b9bd;
  transition: all 0.3s ease-in-out;
  border: 0;
}
.tns-nav [data-nav]:after {
  border-radius: 50%;
  overflow: hidden;
  content: "";
  position: absolute;
  bottom: 0;
  height: 0;
  left: 0;
  width: 100%;
  background-color: #b4b9bd;
  box-shadow: 0 0 1px #b4b9bd;
  transition: height 0.3s ease-in-out;
}
.tns-nav .tns-nav-active:after {
  height: 100%;
}

.dots-inside .tns-nav {
  top: auto;
  padding-bottom: 20px;
}

.dots-end .tns-nav {
  top: 50%;
  bottom: auto;
  transform: translateY(-50%);
  padding: 0;
  right: 30px;
  left: auto;
}
.dots-end .tns-nav [data-nav] {
  display: block;
  margin: 6px;
}

/*dots position*/
.dots-right-top .tns-nav {
  position: absolute;
  top: -55px;
  right: 0;
  left: auto;
}

.dots-primary .tns-nav [data-nav] {
  box-shadow: inset 0 0 0 2px #4cbd89;
  transition: all 0.3s ease-in-out;
}
.dots-primary .tns-nav [data-nav]:after {
  background-color: #4cbd89;
  box-shadow: 0 0 1px #4cbd89;
}

.dots-white .tns-nav [data-nav] {
  box-shadow: inset 0 0 0 2px #ffffff;
  transition: all 0.3s ease-in-out;
}
.dots-white .tns-nav [data-nav]:after {
  background-color: #ffffff;
  box-shadow: 0 0 1px #ffffff;
}

.dots-dark .tns-nav [data-nav] {
  box-shadow: inset 0 0 0 2px #343a40;
  transition: all 0.3s ease-in-out;
}
.dots-dark .tns-nav [data-nav]:after {
  background-color: #343a40;
  box-shadow: 0 0 1px #343a40;
}

.dots-bordered .tns-nav [data-nav] {
  position: relative;
  height: 17px;
  width: 17px;
  box-shadow: inset 0 0 0 2px transparent;
}
.dots-bordered .tns-nav [data-nav]:after {
  content: "";
  position: absolute;
  top: 6px;
  left: 6px;
  height: 5px;
  width: 5px;
  background: #343a40;
  display: block;
}
.dots-bordered .tns-nav .tns-nav-active {
  box-shadow: inset 0 0 0 2px #343a40;
}

.dots-white.dots-bordered [data-nav]:after {
  background: #ffffff;
}
.dots-white.dots-bordered .tns-nav-active {
  box-shadow: inset 0 0 0 2px #ffffff;
}

.dots-start-bottom .tns-nav {
  left: 0;
  transform: none;
}

